    left top,
    left bottom,
    color-stop(0, #e4f0ff),
    color-stop(50%, #dfedff),
    color-stop(100%, #d5e8ff)
  );
  background-image: -o-linear-gradient(
    top,
    #e4f0ff 0,
    #dfedff 60%,
    #d5e8ff 100%
  );
  background-position: 0 1px;
  background-repeat: repeat-x;
}
.gantt_tooltip {
  box-shadow: 3px 3px 3px rgba(0, 0, 0, 0.07);
  border-left: 1px solid rgba(0, 0, 0, 0.07);
  border-top: 1px solid rgba(0, 0, 0, 0.07);
  font-size: 8pt;
  color: #1e2022;
}
.gantt_container,
.gantt_tooltip {
  background-color: #fff;
  font-family: Tahoma;
}
.gantt_container {
  font-size: 11px;
  border: 1px solid #a4bed4;
  position: relative;
  white-space: nowrap;
  overflow-x: hidden;
  overflow-y: hidden;
}
.gantt_touch_active {
  overscroll-behavior: none;
}
.gantt_task_scroll {
  overflow-x: scroll;
}
.gantt_grid,
.gantt_task {
