  overflow-x: hidden;
  overflow-y: hidden;
  display: inline-block;
  vertical-align: top;
}
.gantt_grid_scale,
.gantt_task_scale {
  color: #42464b;
  border-bottom: 1px solid #a4bed4;
  box-sizing: border-box;
}
.gantt_grid_scale,
.gantt_task_scale,
.gantt_task_vscroll {
  box-shadow: inset 0 1px 1px #fff;
